export default{
    times(value) {
        return moment(value).formate('YYYY-MM-DD hh:mm:ss')
    },
    
    cktype(value) {
        var str = value.toLowerCase();
        
        switch(str) {
            case 'radio':
                return '';
                break;
            case 'checkbox':
                return '';
                break;
            case 'textarea':
                return '';
                break;
        }
    },
    
}


