
var data = function(op) {
    
    var titles = [],
        datas = [];
    
    op.content.forEach(function(key, i) {
        titles.push(key.text);
        datas.push(key.pic);
    });
    
    var option = {
        tooltip : {
            trigger: 'axis',
            axisPointer : {            // 
                type : 'shadow'        // 'line' | 'shadow'
            }
        },
        color:['#42b983'],
        legend: {
            data: [op.title]
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        xAxis:  {
            type: 'value'
        },
        yAxis: {
            type: 'category',
            data: titles
        },
        series: [

            {
                name: op.title,
                type: 'bar',
                stack: '',
                label: {
                    normal: {
                        show: true,
                        position: 'insideRight'
                    }
                },
                data: datas
            },
            
        ]
    };
    return option;
}

export default data;


