<template>

    <div class="table-content">
       <p>Optional table caption.</p>
        <div class="table-l">
            <table class="table center">
                <thead>
                    <tr>
                        <th height="50">
                            <input 
                                type="checkbox" 
                                @click="clickAll($event)" 
                                :checked="allcheck"
                                :disabled="isdisable">
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="check in checks">
                        <td height="45">
                            <input type="checkbox" v-model="check.ck">
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="table-r">
            <table class="table center">
              <thead>
                <tr>
                  <th height="50">ID</th>
                  <th>Title</th>
                  <th>State</th>
                  <th>Satr Time</th>
                  <th>End Time</th>
                  <th>
                      <a class="btn btn-other" v-link="'creat'" role="button">Creat</a> 
                  </th> 
                </tr>
              </thead>
              <tbody>  
                <tr v-for="item in formList">
                  <td height="45">     
                      {{item.id}}
                  </td>
                  <td>{{item.title}}</td>
                  <td>{{item.state}}</td>
                  <td>{{item.now | times }}</td>
                  <td>{{item.end | times }}</td>
                  <td class="tab-btn">
                      <a 
                          class="btn btn-primary"
                          v-link="{name:'see', params:{ id: item.id } }"
                          v-if=" item.state !== 'draft' "  
                          role="button">
                          
                      </a>

                      <button 
                         class="btn btn-primary" 
                         v-link="{name:'edit', params:{id: item.id }}" 
                         v-if=" item.state === 'draft' ">
                          
                      </button>

                      <button 
                         class="btn btn-other" 
                         v-if=" item.state === 'draft' " 
                         @click="state($index, item, 'sent')">
                          
                      </button>

                      <button 
                         class="btn btn-default" 
                         v-if=" item.state === 'sent' " 
                         @click="state($index, item, 'end')">
                          
                      </button>

                      <button 
                         class="btn btn-default" 
                         @click="showPop(item)" 
                         v-if="item.state !== 'sent'">
                          
                      </button>
                    
                      <button 
                         class="btn btn-blue"
                         v-link="{name:'write', params:{ id: item.id } }" 
                         v-if="item.state === 'sent'">
                          
                      </button>            
                      
                  </td>  
                </tr>
              </tbody>
            </table>
        </div>
    </div>
    <div class="container">
        <button class="btn btn-default" @click="delAll">
            
        </button>
         <em class="error" v-if="isError">
             <span>*</span>
             <i>,</i>
         </em>
    </div>
    <pop 
        :pop.sync="pop" 
        :form-list.sync="formList" 
        :checks.sync="checks" 
        v-if="pop.isShowPop">
    </pop>
    
</template>


<script>
    import store from '../store';
    import pop from '../components/pop';
    import filter from '../filter';

      
    export default{
        data() { 
            
            let serverData = store.fetch();
            
            return {
                type: 'success',
                user: serverData.user,
                allId: serverData.allId,
                formList: serverData.formList,
                checks:[],
                isError: false,
                pop: {
                    isShowPop: false,
                    msg:'?',
                    id: []
                }
            }
        },
        
        ready() {
            
            //@  | 
            this.update();
        },
        
        //@
        computed:{
            allcheck() {
                var i = 0;
                var len = this.checks.length;
                
                var result;
                this.checks.forEach(function(key, k){
                    if(key.ck) {
                        i++
                    }
                    if (i == len) result = true;
                });
                
                return result;
                
            }
        },
        
        methods: {
            
            update(){ 
                //@  | 
                var self = this;
                this.checks = [];
                this.formList.forEach(function(key, i) {
                    self.checks.push({
                        id: key.id,
                        ck: false,
                        state: key.state
                    })
                })
            },
            //@
            showPop(item) {
                
                if(!Array.isArray(item)){
                    this.pop.id = [item.id];
                } else {
                    this.pop.id = item;
                }
                 
                this.pop.isShowPop = true;  
            },
            
            //@
            state(i, item, state) {
                this.formList[i].state = state;
                this.update();
            },
            
            //@    | -
            clickAll(ev) {
                var elCk = ev.srcElement.checked;
                var self = this;
                self.pop.id = [];
                
                if(elCk) {
                    
                    this.checks.forEach(function(key, i){
                        key.ck = true;
                        self.pop.id.push(key.id)
                    })
                    
                } else{
                    
                    this.checks.forEach(function(key, i){
                        key.ck = false;
                    })
                }
                
            },
            
            //@
            delAll() {
                var idArr = [];
                var self = this;
                
                this.isError = false;
                
                this.checks.forEach(function(key, i) {
                    if (key.ck) {
                        idArr.push(key.id)
                        
                        //@
                        if (key.state == 'sent') {
                           self.isError  = true;
                        }
                    }
                });

                if (idArr.length == 0 || this.isError) return false;
                
                
                this.showPop(idArr);
            }
            
        },
        
        //formList
        watch: {
            formList: {
                deep: true,
                
                //@
                handler() {
                    
                    //@
                    this.formList.forEach(function(key, i) {
                        if (key.end <= key.now) {
                            key.state = 'end'
                        }
                    });
                    
                    //@
                    store.save({
                        allId: this.allId,
                        user: this.user,
                        formList: this.formList
                    })
                }
            }
        },
        
        filters: filter,
        
        //@    |  -
        route:{
            data(transition) {
                let serverData = store.fetch();
            
                if (serverData.formList != this.formList) {
                    this.formList = serverData.formList;
                }
                
                if(serverData.allId != this.allId) {
                    this.allId = serverData.allId
                }
                
                
                this.update();
            }
        },
        
        components: {
            pop
        }
        
    }
    
</script>


